#reasons .list_container {
  display: flex;
  width: 80%;
  margin-left: auto;
  margin-right: auto; }
  @media (min-width: 768px) {
    #reasons .list_container {
      height: calc(40vw + 70px);
      background: url("../img/reasons-bg.png") center center no-repeat;
      background-size: cover;
      position: relative; } }
  #reasons .list_container .icon {
    display: none; }
    @media (min-width: 768px) {
      #reasons .list_container .icon {
        display: inline-block;
        font-size: 70px;
        margin: auto; } }
  #reasons .list_container ol {
    margin: 0;
    padding: 0;
    width: 100%;
    counter-reset: section;
    list-style-type: none; }
    @media (min-width: 768px) {
      #reasons .list_container ol {
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute; } }
    #reasons .list_container ol li {
      width: 80vw;
      height: 80vw;
      color: #fff;
      text-transform: uppercase;
      text-align: center;
      padding: 20px;
      box-sizing: border-box;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center; }
      #reasons .list_container ol li:first-child {
        background-color: #000; }
      #reasons .list_container ol li:nth-child(2) {
        background: #a08f6e url("../img/list-item-2-bg.png") no-repeat; }
      #reasons .list_container ol li:last-child {
        background-color: #fff;
        color: #000; }
      #reasons .list_container ol li:before {
        counter-increment: section;
        content: counter(section);
        color: rgba(0, 0, 0, 0.1);
        font-family: "Gill Sans MT", "Roboto Light", sans-serif;
        font-size: 150px;
        font-weight: 400;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); }
      #reasons .list_container ol li:first-child:before {
        color: rgba(255, 255, 255, 0.2); }
      @media (min-width: 768px) {
        #reasons .list_container ol li {
          position: absolute;
          width: 20vw;
          height: 20vw; }
          #reasons .list_container ol li:first-child {
            top: 0;
            left: 0; }
          #reasons .list_container ol li:nth-child(2) {
            top: 2.7vw;
            right: 0; }
          #reasons .list_container ol li:last-child {
            top: calc(50% + 35px);
            left: calc(50% + 35px); }
          #reasons .list_container ol li:before {
            font-size: 170px; } }
      @media (min-width: 980px) {
        #reasons .list_container ol li:before {
          font-size: 212px; } }

